export default {
	name:"detailed",
	props: {
		day_yq_earnings:{
			type:String,
			default:"0.00"
		}, //今日邀请收益
		all_yq_earnings:{
			type:String,
			default:"0.00"
		}, //累计个人收益
		day_td_earnings: {
			type:String,
			default:"0.00"
		}, //今日团队收益
		all_td_earnings: {
			type:String,
			default:"0.00"
		}, //累计团队收益
		day_jd_earnings: {
			type:String,
			default:"0.00"
		}, //节点收益
		all_jd_earnings: {
			type:String,
			default:"0.00"
		} //超级节点收益
	},
	data() {
		return {
			isShow:true
		}
	},
	computed:{
		earnings() {
			if(this.isShow) {
				return [
					{
						name:"今日邀请收益",
						value:this.day_yq_earnings
					},
					{
						name:"今日团队收益",
						value:this.day_td_earnings
					},
					{
						name:"累计个人收益",
						value:this.all_yq_earnings
					},
					{
						name:"累计团队收益",
						value:this.all_td_earnings
					},
					{
						name:"节点收益",
						value:this.day_jd_earnings
					},
					{
						name:"超级节点收益",
						value:this.all_jd_earnings
					}
				]
			}else {
				return [
					{
						name:"今日邀请收益",
						value:this.day_yq_earnings
					},
					{
						name:"今日团队收益",
						value:this.day_td_earnings
					},
					{
						name:"累计个人收益",
						value:this.all_yq_earnings
					},
					{
						name:"累计团队收益",
						value:this.all_td_earnings
					}
				]
			}
		},
		showWidth() {
			if(this.isShow==true) {
				return 30+'%';
			}else {
				return 45+'%';
			}
		}
	}
}
